import React, {useState} from 'react';
import Star from '../../../utils/icon/Star';
import {CaseInfo} from '../../../utils/supports';
import {arrowDown, arrowUp} from '../../../common/images/materials';
import './index.scss';

const files = [{title: ' .pdf', size: '76 '}, {title: ' .pdf', size: '76 '}, {title: ' .pdf', size: '76 '}]
const feedback = [
  {
    stars: 3,
    name: '  ',
    text: ' ,        ,     ()     .        , ,  .'
  },
  {
    stars: 5,
    name: '  ',
    text: ' ,        ,     ()     .        , ,  .'
  }
]

const MyCases = () => {
  return (
    <div className='my-cases'>
      <Case title='Railway One'
            text='   FastCom Group,       .      .  FastCom        ,  FastCom Bank.          ,       .           .'
            files={files}
            feedback={feedback}/>
    </div>
  );
};

export default MyCases;

const Case = ({title, text, files, feedback}) => {
  const [isShow, setIsShow] = useState(false)

  return (
    <div className='case'>
      <div className='case-top'>
        <h3 className='case-top__title'>{title}</h3>
        <div className='case-top__arrow'>
          <img className={isShow ? 'arrow-up' : 'arrow-down'} src={isShow ? arrowUp : arrowDown} onClick={() => setIsShow(!isShow)} alt='arrow'/>
        </div>
      </div>
      {isShow &&
      <>
        <CaseInfo title=' ' text={text} files={files}/>

        <div className='case-feedback'>
          <div className='case-feedback__title'></div>
          {feedback.map(({stars, name, text}) => <Feedback stars={stars} name={name} text={text}/>)}
        </div>
      </>
      }
    </div>
  )
}

const Feedback = ({stars, name, text}) =>
  <div className='feedback'>
    <div className='feedback__stars'>
      <Stars stars={stars}/>
    </div>
    <div className='feedback__name'>{name}</div>
    <p className='feedback__text'>{text}</p>
  </div>

const Stars = ({stars}) => {
  return Array(7).fill(0).map((_, index) => index < stars ? <Star fill='gold'/> : <Star/>)
}
