import React, {useState} from 'react';
import {Input, Button, Upload} from 'antd';
import {CaseInfo} from '../../utils/supports';
import {arrowDown, arrowUp} from '../../common/images/materials';
import './index.scss';

const op = {
  year: '2-digit',
  month: '2-digit',
  day: '2-digit'
}

const Cases = ({imgTitle}) => {
  const [currentStep, setCurrentStep] = useState(1)
  const [isShow, setIsShow] = useState(false)
  const [stepDate, setStepDate] = useState(new Intl.DateTimeFormat('ru-RU', op).format(new Date()))

  return (
    <div className='cases'>
      <div className='cases__top'>
        <div className='cases__top-card'>
          <div className='cases__top-card__img'>
            <h5 className='cases__top-card__img-title'>{imgTitle}</h5>
            <img src={null} alt='preview'/>
          </div>
          <div className='cases__top-card__info'>
            <h3 className='cases__top-card__info-subtitle'>Operational improvements</h3>
            <h2 className='cases__top-card__info-title'>Elite Furniture</h2>
          </div>
        </div>
      </div>
      <div className='cases__description'>
        {/*<div className='cases__description-title' onClick={() => setIsShow(!isShow)}>*/}
        {/*  */}
        {/*  <img className={isShow ? 'arrow-up' : 'arrow-down'} src={isShow ? arrowUp : arrowDown} alt='arrow'/>*/}
        {/*</div>*/}
        {/*<div className='cases__description-main'>*/}
        {/*  <CaseInfo text={null} files={[]} title='  '/>*/}
        {/*</div>*/}
      </div>
      <div className='cases__steps'>
        <Steps step={currentStep}/>
      </div>
      <div className='cases__solution'>
        <YourSolution/>
      </div>
    </div>
  );
};

export default Cases;

const Steps = ({step}) => {
  return (
    <div className='steps'>
      <div className='steps-info'>
        <div className='steps-info__current-step'> {step}/4</div>
        <div className='steps-info__allotted-date'>07.12.2020  04.01.2021</div>
        <div className='steps-info__left-date'> : <span>24  4 </span></div>
      </div>
      <TitleStep title=' ' text='       :  ,   '/>
    </div>
  )
}

const TitleStep = ({title, text}) => {
  return (
    <div className='title-step'>
      <h2 className='title-step__title'>{title}</h2>
      <p className='title-step__text'>{text}</p>
    </div>
  )
}


const YourSolution = ({step}) => {
const onChange = e => {
  debugger
  console.log(e)
}

  return (
    <div className='solution'>
      <div className='solution__title'>
         
      </div>
      <div className='solution__main'>
        <Input.TextArea placeholder=' '/>
        <Upload onChange={e => console.log(e)}>
            0/5
        </Upload>
        <Button> </Button>
      </div>
    </div>
  )
}
