import React, {useState} from 'react';
import {Input, Button} from 'antd';
import cn from 'classnames';
import './index.scss';

const modal = [
  {
    reason: 'delete',
    title: '      ?',
    text: '        .',
    btn1: '',
    btn2: ',  '
  },
  {reason: 'cancel', title: ' ,        ?', btn1: ', ', btn2: '  '},
  {
    reason: 'no add',
    title: '       ?',
    text: '      ',
    btn1: ', ',
    btn2: ',  '
  }
]

const ModalCard = ({reason, setModal}) => {
  return (
    <div className={cn('modal-card', {'dn': !reason})}>
      <div className="modal-card__modal">
        <div className="modal-card__close" onClick={() => setModal(null)}>----</div>
        {reason === 'report'
          ? <Report/>
          : modal.map(m => m.modal === reason && <ContactChange {...m}/>)}
      </div>

    </div>
  )
}

export default ModalCard;

const ContactChange = ({title, text, btn1, btn2}) => {
  return (
    <div className='contact-change'>
      <div className='contact-change__title'>
        {title}
      </div>
      <div className='contact-change__text'>
        {text}
      </div>
      <Button className='blue'>
        {btn1}
      </Button>
      <Button>
        {btn2}
      </Button>
    </div>
  )
}

const Report = () => {
  const [text, setText] = useState('')
  const [isSent, setIsSent] = useState(false)
  return (
    <div className='report'>
      {isSent ?
        <>
          <div className='report__title'>Report</div>
          <p className='report__text'>  . ,     .</p>
          <Input.TextArea placeholder=' ' value={text} onChange={setText}/>
          <Button className='blue' onClick={() => setIsSent(true)}></Button>
        </>
        : <>
          <div className='report__title'> </div>
          <p className='report__text'>    Case Academy.       .   .</p>
          <Button></Button>
        </>
      }
    </div>
  )
}
