import React, {useState, useMemo} from 'react';
import {connect} from 'react-redux';
import cn from 'classnames';
import Avatar from '../../utils/avatar';
import Contacts from './Contacts';
import MyCases from './MyCases';
import Profile from './Profile';
import Settings from './Settings';
import {pen} from '../../common/images/account';
import './index.scss';

const menu = [
  {tab: 'Contacts'},
  {tab: 'Profile'},
  {tab: 'My cases'},
  {tab: 'Settings'}
]

const PrivateAccount = ({page}) => {
  const [currentShowBlock, setCurrentShowBlock] = useState(page || 'Contacts')
  const [src, setSrc] = useState(null)

  const showBlock = useMemo(() => {
    switch (currentShowBlock) {
      case 'Contacts': {
        return <Contacts/>
      }
      case 'Profile': {
        return <Profile/>
      }
      case 'My cases': {
        return <MyCases/>
      }
      case 'Settings': {
        return <Settings/>
      }
      default: {
        return <div>  ?????</div>
      }
    }
  }, [currentShowBlock])

  return (
    <div className='account'>
      <div className='account__top'>
        {/*<div className='account__top-avatar'>*/}
        {/*  <Avatar src={src} setSrc={setSrc} initials='NMR' icon={pen} />*/}
        {/*</div>*/}
        {/*<div className='account__top-name'>*/}
        {/*  Nepomuceno Mara Remedios*/}
        {/*</div>*/}
      </div>
      <div className='account__main'>
        <div className='account__main-menu'>
          <ul onClick={e => e.target.nodeName === 'LI' && setCurrentShowBlock(e.target.innerText)}>
            {menu.map(({tab}) => <li key={tab} className={cn('account__main-menu__list', {'color-black': tab === currentShowBlock})}>{tab}</li>)}
          </ul>
        </div>
        <div className='account__main-wrapper'>
          {showBlock}
        </div>
      </div>
    </div>
  );
};

const mstp = ({page}) => ({...page})

export default connect(mstp)(PrivateAccount);
